body {
    background-image: url('../assets/background.jpg');
}

#navbar {
    background-color: #fff;
}

#main {
    min-height: 80vh;
}

#name {
    background-color: rgb(21, 114, 75);
}

#name-text {
    color: #fff;
}

#links-container {
    list-style-type: none;
}

.links {
    font-weight: bold;
    transition: all 0.5s;
}

.links:hover {
    color: rgb(21, 114, 75);
    cursor: pointer;
    font-weight: bold;
}

#footer {
    background-color: grey;
    color: #fff;
    border-top: 8px solid rgb(21, 114, 75);
}

#profile-pic {
    float: left;
    width: 20vw;
}

.fab {
    color: grey;
    transition: all 0.5s;
}

#github-link:hover {
    color: #000;
    cursor: pointer;
}

#linkedin-link:hover {
    color: rgb(44, 107, 209);
    cursor: pointer;
}

#stack-link:hover {
    color: rgba(223, 160, 26, 0.966);
    cursor: pointer;
}

.text {
    color:rgb(21, 114, 75)
}

#socials {
    height: fit-content;
    background-color: #fff;
}

#about, #gallery {
    /*height: fit-content;*/
    background-color: #fff;
}

button {
    background-color: rgb(21, 114, 75);
    border: none;
    border-radius: 10px;
    color: #fff;
    transition: all 1s;
}

#project-pic {
    max-width: 175px;
}

.project-container {
    width: fit-content;
    position: relative;
}

#project-name {
    background-color: rgb(21, 114, 75);
    color: white;
}

hr {
    color: rgb(21, 114, 75);
}

#active {
    color: rgb(21, 114, 75);
    font-weight: bold !important;
}

#submit-button:hover {
    background-color: rgb(14, 65, 46);
}